import { Component, Host, Prop, State, h } from '@stencil/core';
import { ApiCart } from '../../../../../utils/api/ecommerce/cart.api';
import { ApiProduct } from '../../../../../utils/api/ecommerce/product.api';
import { CartItem } from '../../../../../utils/api/ecommerce/utils/interfaces/cart.interface';
import { Product } from '../../../../../utils/api/ecommerce/utils/interfaces/product.interface';
import { currency } from '../../../../../utils/filters/currency.filter';

@Component({
  tag: 'tp-elem-related-products',
  styleUrl: 'related-products.scss',
  scoped: true,
})
export class TpElemRelatedProducts {
  /**
   * ID do produto
   */
  @Prop({ reflect: true }) productId: number;

  /**
   * Items constantes no carrinho.
   */
  @State() cart: CartItem[] = [];

  /**
   * Estado de carregamento do componente.
   */
  @State() loadingProducts: boolean;

  /**
   * Um conjunto de objetos com todos os dados dos produtos à mostra.
   */
  @State() products: Product[];

  async componentWillLoad() {
    this.getRelatedProducts();
    this.getCurrentCart();
  }

  async getRelatedProducts() {
    this.loadingProducts = true;

    const { relatedIds } = await ApiProduct.getProductById(this.productId);
    const productsPromises: Promise<Product>[] = [];

    relatedIds.forEach((id) => {
      const productPromise = ApiProduct.getProductById(id);
      productsPromises.push(productPromise);
    });

    const products = await Promise.all(productsPromises);
    this.products = products;

    this.loadingProducts = false;
  }

  async getCurrentCart() {
    try {
      const { cart } = await ApiCart.getCart();
      this.cart = cart;
    } catch (error) {
      console.log('Não foi possível ter acesso ao carrinho.');
    }
  }

  async onAddToCart(productId: number) {
    try {
      const { success, cart } = await ApiCart.addToCart(productId, 1);
      if (success) this.cart = cart;
    } catch (error) {
      console.log('Não foi possível adicionar item ao carrinho.');
    }
  }

  async onRemoveFromCart(id: number) {
    try {
      const { success, cart } = await ApiCart.removeFromCart(id);
      if (success) this.cart = cart;
    } catch (error) {
      console.log('Não foi possível remover o item do carrinho.');
    }
  }

  render() {
    const { cart, loadingProducts, products } = this;

    let productItems;

    if (loadingProducts) {
      productItems = (
        <ion-text color="neutral-8">
          <p>Carregando...</p>
        </ion-text>
      );
    } else {
      productItems =
        products && products.length > 0 ? (
          products.map((product) => {
            const {
              permalink,
              id,
              name,
              regularPrice,
              price,
              stockQuantity,
              manageStock,
              stockStatus,
            } = product;

            const alreadyOnCart = cart.some((item) => item.id === id);
            const haveInStock =
              (manageStock && stockQuantity >= 1) ||
              (!manageStock && stockStatus === 'instock');

            return (
              <tp-elem-product-item
                class="related-products__item"
                product={product}
              >
                <ion-text slot="title" tp-type="p18xb" color="neutral-10">
                  <h3>
                    <a href={permalink}>{name}</a>
                  </h3>
                </ion-text>

                <ion-text slot="regular-price" color="neutral-6" tp-type="p14">
                  <p>{currency(regularPrice, 'pt-br')}</p>
                </ion-text>

                <ion-text slot="price" color="neutral-8">
                  <p>{currency(price, 'pt-br')}</p>
                </ion-text>

                {haveInStock && !alreadyOnCart && (
                  <ion-button
                    slot="button"
                    color="neutral-2"
                    expand="block"
                    onClick={() => this.onAddToCart(id)}
                  >
                    Adicionar ao Carrinho
                  </ion-button>
                )}

                {haveInStock && alreadyOnCart && (
                  <ion-button
                    slot="button"
                    color="warning"
                    expand="block"
                    onClick={() => this.onRemoveFromCart(id)}
                  >
                    Remover do Carrinho
                  </ion-button>
                )}
              </tp-elem-product-item>
            );
          })
        ) : (
          <ion-text color="neutral-8">
            <p>Não há produtos relacionados.</p>
          </ion-text>
        );
    }

    return (
      <Host>
        <div class="related-products">{productItems}</div>
      </Host>
    );
  }
}
